<template>
    <div id="chart">
      <ApexChart type="area" height="350" :options="chartOptions" :series="series"></ApexChart>
    </div>
  </template>
  
<script setup>
import { ref, watch, defineProps } from 'vue';
import ApexChart from 'vue3-apexcharts';
const props=defineProps({
    currencyData:Object,
    countryNumber:Number
});
const series = ref([{
    name: 'series-1',
    data: [30, 40, 35, 50, 49, 60, 70, 91]
}]);

const chartOptions = ref({});

watch([() => props.currencyData, () => props.countryNumber], ([newCurrency, newCountryNumber]) => {
    if (!newCurrency || newCountryNumber === undefined) return; //    countryNumber    early return

    const newCategories = newCurrency[0].slice(7);
    chartOptions.value = {
        chart: {
            id: 'vuechart-example'
        },
        xaxis: {
            categories: newCategories
        }
    };
    series.value[0] = {
        name: newCurrency[newCountryNumber][1],
        data: newCurrency[newCountryNumber].slice(7).map(item => parseFloat(item.replace(/,/g, ''))) //    
    };
}, { deep: true }); // deep     


</script>
  