<template>
  <div>
    <!--    -->
    <button @click="fetchReservation" class="btn btn-primary"> </button>
    <!--   -->
    <transition name="modal">
      <div v-if="showModal" class="modal" tabindex="-1" role="dialog" @click="closeModal">
        <div class="modal-dialog centered modal-lg" role="document" @click.stop>
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title"> </h5>
              <button type="button" class="close-icon" @click="closeModal">
                <i class="fas fa-times"></i>
              </button>
            </div>
            <div class="modal-body">
              <!--    -->
              <h6>  </h6>
              <table class="table">
                <thead>
                  <tr>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                    <th scope="col"> </th>
                    <th scope="col"></th>
                    <th scope="col"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item, index in reservationItems" :key="index">
                    <td>{{ item.reservation_no }}</td>
                    <td>{{ item.reservation_purchastime }}</td>
                    <td @click="textClick([item.reservation_lat,item.reservation_lng])">{{ item.reservation_pricename }}</td>
                    <td>{{ Number(item.reservation_price).toLocaleString() }}</td>
                    <td><a :href="item.reservation_receipturl" target="_blank"> </a></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script setup>
import { ref, defineEmits } from 'vue';
import axios from 'axios';

const showModal = ref(false);
const reservationItems = ref([]);

const emit = defineEmits('handleItemClick');

const textClick = (value) => {
  console.log(value)
  emit('handleItemClick', value);
}

async function fetchReservation() {
  const vuexStore = JSON.parse(localStorage.getItem('vuex'));
  const userId = vuexStore.loginStore.userInfo.id;
  
  //   
  const response = await axios.get(`${process.env.VUE_APP_API_URL}/reservation`, {
    params: { userId }
  });

  //      
  reservationItems.value = response.data.sort((a, b) => {
    const dateA = new Date(a.reservation_purchastime);
    const dateB = new Date(b.reservation_purchastime);
    return dateB - dateA;  
  });
  console.log(reservationItems.value[0].reservation_lat)
  showModal.value = true;
}

const closeModal = () => {
  showModal.value = false; 
};
</script>

<style scoped>
.modal {
  display: block;
  position: fixed;
  z-index: 1050;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-dialog.modal-lg {
  max-width: 70%; /*    %  px  . */
}

.modal-dialog {
  position: relative;
  margin: 10% auto;
  padding: 10px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
.modal-header {
  position: relative;
  padding: 10px;
  background-color: #e6e6fa; /*    */
  color: #333333; /*     */
  border-radius: 10px 10px 0 0; /*      */
}
.modal-title {
  margin: 0;
  font-size: 1.8rem;
  text-align: center; /*    */
}
.modal-body {
  padding: 20px;
}

/*   */
.modal-enter-active, .modal-leave-active {
    transition: opacity .5s;
  }
.modal-enter-from, .modal-leave-to {
  opacity: 0;
}
.modal-enter-to, .modal-leave-from {
  opacity: 1;
}

/*    */
.close-icon {
  position: absolute;
  right: 20px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  color: black; /*   */
}
</style>
